import React from "react";

import Slider from "react-slick";

import {translate} from "../../i18n/translate";

import styles from './index.module.scss';

const Header = () => {
    const settings = {
        dots: true,
        arrows: false,
        infinite: true,
        autoplay: true,
        speed: 500,
        autoplaySpeed: 5000,
        slidesToShow: 1,
        slidesToScroll: 1,
        fade: true
    };

    return (
        <header className={styles.block}>
           <Slider {...settings}>
               {
                   Array(3).fill(null).map((item, idx) =>
                       <React.Fragment key={idx}>
                           <div className={styles.picture}>
                               <picture>
                                   <source media="(max-width: 576px)" srcSet={`/img/service/576/1x/${idx + 1}.webp 1x, /img/service/576/2x/${idx + 1}@2x.webp 2x`}/>
                                   <source media="(max-width: 768px)" srcSet={`/img/service/768/1x/${idx + 1}.webp 1x, /img/service/768/2x/${idx + 1}@2x.webp 2x`}/>
                                   <source media="(max-width: 992px)" srcSet={`/img/service/992/1x/${idx + 1}.webp 1x, /img/service/992/2x/${idx + 1}@2x.webp 2x`}/>
                                   <source media="(max-width: 1200px)" srcSet={`/img/service/1200/1x/${idx + 1}.webp 1x, /img/service/1200/2x/${idx + 1}@2x.webp 2x`}/>
                                   <source media="(max-width: 1600px)" srcSet={`/img/service/1600/1x/${idx + 1}.webp 1x, /img/service/1600/2x/${idx + 1}@2x.webp 2x`}/>
                                   <source media="(min-width: 2048px)" srcSet={`/img/service/2048/1x/${idx + 1}.webp 1x, /img/service/2048/2x/${idx + 1}@2x.webp 2x`}/>
                                   <img src={"/img/service/1600/1x/1.webp"} alt={`Slide ${idx + 1}`} />
                               </picture>
                           </div>
                           <div className={styles.content}>
                               <h2 className={styles.title}>{translate(`header_title_${(idx + 1)}`)}</h2>
                               <h4 className={styles.subtitle}>{translate(`header_subtitle_${idx + 1}`)}</h4>
                           </div>
                       </React.Fragment>
                   )
               }
           </Slider>
        </header>
    );
}

export default Header;
